.across p {
  padding: 0;
  margin-bottom: 3rem;
}

.across .apply-btn1 {
  font: var(--font-18);
  color: var(--color-4e);
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 0.72px;
  background-color: var(--color-100);
  padding: 1rem 1.6rem;
}

.across .apply-btn1:hover {
  background-color: var(--color-4e);
  color: var(--color-white);
}

.testimonial-img {
  position: relative;
}

.testimonial-img-box {
  position: relative;
}

.testimonial-img-box img {
  border-radius: 54px 8px 8px 8px;
}

.testimonial-img-text {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 99;
  transition: all 0.4s;
  padding: 6rem 2rem 3rem 3rem;
  width: 100%;
  background: transparent linear-gradient(0deg, #000000 0%, #00000000 100%) 0% 0% no-repeat padding-box;
}

.testimonial-img-text h4 {
  color: var(--color-white);
  font: var(--font-36);
  text-transform: uppercase;
  font-weight: 600;
  font-family: var(--font-Condensed);
}

.testimonial-img-text p {
  color: var(--color-white);
  font: var(--font-18);
  font-weight: 600;
  font-family: var(--font-Condensed);
}

.testimonial-img-text p span {
  color: var(--color-100);
  font-size: 2rem;
}

.testimonial-img-text blockquote {
  color: var(--color-white);
  font: var(--font-16);
  font-weight: 300;
  margin: 1rem 0 0 0;
}

.testimonial-img-text::before {
  content: '';
  background-color: var(--color-100);
  position: absolute;
  left: 0;
  width: 4px;
  height: 11rem;
}

.opportunitie-sec {
  position: relative;
}

.opportunitie-img {
  position: relative;
}

.opportunitie-box {
  position: absolute;
  bottom: 5rem;
}

.opportunitie-text {
  width: 65rem;
}

.opportunitie-text blockquote {
  font: var(--font-18);
  color: var(--color-4e);
  text-transform: uppercase;
  font-weight: 600;
  font-family: var(--font-Condensed);
}

.opportunitie-text h2 {
  font: var(--font-42);
  color: var(--color-4e);
  text-transform: uppercase;
  font-weight: 600;
  font-family: var(--font-Condensed);
  letter-spacing: -1.26px;
}

.opportunitie-text h2 span {
  color: var(--color-e8);
}

.opportunitie-tab {
  background-color: #00000066;
  border-radius: 8px;
  padding: 1rem;
  margin-top: 9rem;
}

.opportunitie-tab ul {
  margin: 0;
  padding: 0;
}

.opportunitie-tab ul li {
  display: inline-block;
  padding-right: 4.5rem;
}

.opportunitie-tab ul li a {
  color: var(--color-white);
  font: var(--font-16);
  position: relative;
  padding-inline: .8rem 0;
}

.opportunitie-tab ul li a:hover {
  color: var(--color-100);
}

.opportunitie-tab ul li a::before {
  content: '';
  background-color: var(--color-100);
  width: .2rem;
  height: 2.2rem;
  position: absolute;
  left: 0;
}